.news-page {
  flex-grow: 1;
  margin: 0 auto 40px;

  .page__inner {
    max-width: var(--desctop-width);
    margin: 0 auto;
    padding: 0 15px;
  }

  &__title {
    margin-bottom: 30px;

  }

  &__img {
    margin-bottom: 30px;
    width: 100%;
    height: auto;

    img {
      width: inherit;
    }
  }

  &__content {
    max-width: 874px;
    margin: 0 auto;


    .news_page__lead {
      font-size: 18px;
      line-height: 150%;
      margin-bottom: 40px;

    }


    .news-page__text {
      font-size: 15px;
      line-height: 150%;
      margin-top: 40px;
      margin-bottom: 40px;

      a {
        border-bottom: 1px solid var(--main-color-hover);
      }

      p {
        padding: 5px 0;

        &:first-child {
          padding-top: 0;
        }

        &:last-child {
          padding-bottom: 0;
        }
      }
    }
  }
}

.news-page__article.page__wrapper {
  max-width: 1096px !important;

  .news-list__inner {
    margin-top: 40px;
  }
}

@media (min-width:1024px) {
  .news-page {
    margin-bottom: 85px;

    .page__inner {
      padding: 0 60px;
    }

    &__title {
      margin-bottom: 40px;
    }

    &__img {
      margin-bottom: 40px;
    }

    &__content {
      .news_page__lead {
        font-size: 24px;
      }


      .news-page__text {
        font-size: 20px;

        p {
          padding: 10px 0;

          &:first-child {
            padding-top: 0;
          }

          &:last-child {
            padding-bottom: 0;
          }
        }
      }
    }


  }

  .news-page__article {
    .news-list__wrapper {
      .news-list__inner {

        border-top: var(--border) var(--color-45b);
        padding-top: 40px;

      }
    }
  }
}

@media (min-width: 1440px) {
  .news-page__article {
    .news-list__wrapper {
      .news-list__inner {
        width: 100%;
      }
    }
  }
}